.App {
  text-align: center;
  background-color: #ffe4e1; /* 粉色背景 */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(12px + 2vmin);
  color: #ff69b4;
  font-family: 'Comic Sans MS', 'Poppins', 'Quicksand', sans-serif;
}

.title {
  font-size: 28px;
  font-weight: bold;
  color: #ff1493;
  margin-bottom: 20px;
  text-shadow: 2px 2px 4px rgba(255, 105, 180, 0.5);
}

.account-text {
  font-size: 16px;
  background: #ffb6c1;
  padding: 8px 16px;
  border-radius: 15px;
  color: white;
  box-shadow: 2px 2px 6px rgba(255, 105, 180, 0.3);
  margin-bottom: 20px;
}

.button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px; /* 讓按鈕之間有空隙 */
  margin-top: 20px;
}

.cute-button {
  background: linear-gradient(to right, #ff9a9e, #fad0c4);
  border: none;
  border-radius: 20px;
  padding: 12px 24px;
  font-size: 18px;
  font-weight: bold;
  color: white;
  cursor: pointer;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease-in-out;
  margin: 10px;
}

.cute-button:hover {
  transform: scale(1.1);
  box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.2);
}

.mint-button {
  margin-top: 10px;
}

.mint-button:disabled {
  background: #cccccc;
  cursor: not-allowed;
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 4px solid rgba(255, 105, 180, 0.3);
  border-top: 4px solid #ff69b4;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-top: 10px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
